import aosMixin from '../mixins/aos'
export default {
  name: 'AboutPage',
  transition: 'fade',
  mixins: [aosMixin],
  nuxtI18n: {
    paths: {
      en: '/about-us', // -> accessible at /about-us
      tr: '/hakkimizda', // -> accessible at /fr/a-propos
