  head() {
    return {
      title: this.$store.state.sitename + this.$t('about.title'),
      htmlAttrs: {
        lang: this.$i18n.locale,
      },
      meta: [
        {
          hid: 'description',
          name: 'description',
          content: this.$t('about.description'),
